* {
    margin: 0;
    padding: 0;
}

body {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

table {
    width: 60vw;
    height: 40vh;
}

thead, th {
    width: 20vw;
    height: 10vh;
    background-color: #9fcdff;
}

tbody {
    width: 20vw;
    height: 30vh;
    background-color: #9fcdff;
}

td {
    text-align: center;
    width: 20vw;
    height: 5vh;

}

button {
    background-color: rgba(71, 137, 208, 0.2);
    width: 5vw;
    height: 7vh;
    outline: none;
    border-color: transparent;
    box-shadow: none;
}

form {
    width: 60vw;
    height: 20vh;
    background-color: #9fcdff;
}

label {
    height: 20vh;
    line-height: 20vh;
    font-size: 1.3em;
}

input {
    height: 5vh;
    text-align: center;
    width: 17vw;
    outline: none;
    border: 0 solid rgba(0, 0, 0, 0);
    border-bottom: 3px solid white;
    background-color: #9fcdff;
}

input:last-child {
    border-bottom: 3px solid rgba(0, 0, 0, .0);
    background-color: rgba(71, 137, 208, 0.2);
}

input:focus {
    border: blue;
}
